/*
 * Simple default browser style reset
 *
 * Based on 'Minimal CSS Reset' by Alligator.io
 * CC-BY-NC-SA 4.0 https://www.digitalocean.com/community/tutorials/css-minimal-css-reset
 */

html {
    box-sizing: border-box;
    font-size: 16px;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
    margin: 0;
    padding: 0;
    font-weight: normal;
}

ol,
ul {
    list-style: none;
}

img {
    max-width: 100%;
    height: auto;
}